import NcListItem from '@nextcloud/vue/components/NcListItem';
import AppManagement from "../../mixins/AppManagement.js";
import { useAppsStore } from "../../store/apps-store";
import { useAppApiStore } from "../../store/app-api-store";
export default {
  name: 'DaemonSelectionEntry',
  components: {
    NcListItem
  },
  mixins: [AppManagement],
  // TODO: Convert to Composition API when AppManagement is refactored
  props: {
    daemon: {
      type: Object,
      required: true
    },
    isDefault: {
      type: Boolean,
      required: true
    },
    app: {
      type: Object,
      required: true
    },
    deployOptions: {
      type: Object,
      required: false,
      default: () => ({})
    }
  },
  setup() {
    const store = useAppsStore();
    const appApiStore = useAppApiStore();
    return {
      store,
      appApiStore
    };
  },
  computed: {
    itemTitle() {
      return this.daemon.name + ' - ' + this.daemon.display_name;
    },
    daemons() {
      return this.appApiStore.dockerDaemons;
    }
  },
  methods: {
    closeModal() {
      this.$emit('close');
    },
    selectDaemonAndInstall() {
      this.closeModal();
      this.enable(this.app.id, this.daemon, this.deployOptions);
    }
  }
};